.elements {
  max-width: 880px;
  margin: 0 auto 66px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px 17px;
}

@media (max-width: 920px) {
  .elements {
    max-width: 584px;
    margin-bottom: 48px;
    gap: 20px;
  }
}

@media (max-width: 630px) {
  .elements {
    max-width: 282px;
  }
}
