.element__delete-icon {
  position: absolute;
  background: url('../../../images/svg/delete_icon.svg') center no-repeat;
  top: 20px;
  right: 20px;
  width: 18px;
  height: 19px;
  border: 0;
  cursor: pointer;
  transition: all 0.5s ease;
}

.element__delete-icon:hover {
  opacity: .6;
}
